/* ---------------animation of main content------------------------------*/
.html_content p {
  animation-duration: 1s;
  animation-name: slidein;
}
@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%;
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}
/*-------------------------end of animation-------------------*/
