input[type="checkbox"] {
  visibility: hidden;
}
.ttr_checkbox {
  position: relative;
  width: 13px;
  height: 13px;
  display: inline;
  margin-top: 5px;
  margin-right: 5px;
}
.ttr_checkbox label {
  cursor: pointer;
  position: absolute;
  width: 15px;
  height: 15px;
  top: 0;
  left: 0;
  background-color: #ffffff;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(0, 0, 0, 0.14) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  background-clip: padding-box;
  border-radius: 0px 0px 0px 0px;
  border: solid #808080;
  border: solid rgba(128, 128, 128, 1);
  border-width: 1px 1px 1px 1px;
  box-shadow: none;
}
.ttr_checkbox label:after {
  opacity: 0;
  content: "";
  position: absolute;
  width: 15px;
  height: 5px;
  top: 0px;
  left: 1px;
  border: 2px solid rgba(0, 0, 0, 1);
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
}
.ttr_checkbox input[type="checkbox"]:checked + label:after {
  opacity: 1;
}
.ttr_checkbox label {
  width: 13px;
  padding-left: 0;
}
input[type="radio"] {
  visibility: hidden;
}
.ttr_radio {
  position: relative;
  width: 13px;
  height: 13px;
  margin-right: 5px;
  margin-bottom: 5px;
}
.ttr_radio label {
  cursor: pointer;
  position: absolute;
  width: 13px;
  height: 13px;
  top: 0;
  left: 0;
  border-radius: 0px 0px 0px 0px;
  border: solid #808080;
  border: solid rgba(128, 128, 128, 1);
  border-width: 1px 1px 1px 1px;
  box-shadow: none;
  border-radius: 50%;
  background-color: #808080;
  background: rgba(128, 128, 128, 1);
  background-clip: padding-box;
  box-sizing: content-box;
}
.ttr_radio label:after {
  background-color: #ffffff;
  background: rgba(255, 255, 255, 1);
  background-clip: padding-box;
  opacity: 0;
  content: "";
  position: absolute;
  width: 9px;
  height: 9px;
  top: 2px;
  left: 2px;
  border-radius: 50%;
}
.ttr_radio input[type="radio"]:checked + label:after {
  opacity: 1;
}
.ttr_radio label {
  width: 13px;
  padding-left: 0;
}
